@import url(https://fonts.googleapis.com/css2?family=Akshar&family=Poppins:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200&display=swap);
header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* =============== CTA =============*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* =============== HEADER SOCIALS =============*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* =============== Me =============*/
.me{
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem; */
    
}
.me > img{
    border-radius: 12rem 12rem 0 0;
}

/* =============== SCROLL DOWN =============*/
.scroll__down{
    position: absolute;
    right: -2.3rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    top: 32rem;
}

/* ============== MEIA QUEIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
    header{
        height: 95vh;
    }
}

/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
   header{
       height: 100vh;
   }

   .header__socials,.scroll__down{
     display: none;
   }
    
}
nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
    color: var(--color-white);
}
.about__container{
    display:grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
     transition: var(--transition);
}

.about__me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align:center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.96rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* ============== MEIA QUEIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
   
  .about__container{
      grid-template-columns: 1fr;
      grid-gap: 0;
      gap: 0;
  }

  .about__me{
      width: 50%;
      margin: 2rem auto 4rem;
  }
  .about__content{
      text-align: center;
  }
  .about__content p{
      margin: 1rem 0 1.5rem;
  }
}

/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {

    .about__me{
        width: 65%;
        margin: 1.5rem auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content{
        text-align: center;
    }
    
    .about__content p{
        margin: 1.5rem 0;
    }
}
.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}
.experience__container > div:hover{
    background: transparent;
    border-color: var(--color-primary);
    cursor: default;
}
.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience__container > div h4{
    font-family: 'Poppins', sans-serif;
}

.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}
.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.experience__details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}
/* ============== MEIA QUEIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience__content{
        padding: 1rem;
    }
    
}

/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container > div{
        width:100%;
        padding: 2rem 1rem;
    }
    
}
.portfolio__container{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}
.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}
.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
    height: 200px;
}
.portfolio__item h3{
    margin: 1.2rem 0 2rem;
    font-size: medium;
}

.portfolio__item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    justify-content: center;
}
/* ============== MEIA QUEIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    
}
.testimonials__container{
    width: 40% !important;
    padding-bottom: 4rem;
}

.team_avatar{
    width: 4rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid var(--color-primary-variant);
}

.testimonial{
    background: var(--color-bg-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.friend__review{
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 1.8rem auto;
}
.swiper-pagination-clickable .swiper-pagination-bullet-active{
    background: var(--color-primary);
}

/* ============== MEIA QUEIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
    .testimonials__container{
        width: var(--container-width-md) !important;
    }

    section{
        margin-top: 6rem;
    }
    
}

/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
    .testimonials__container{
        width: var(--container-width-sm) !important;
    }

    section{
        margin-top: 2rem;
    }
    
}
.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover{
background: transparent;
border-color: var(--color-primary-variant);
}

.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0 1.5rem;
}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/*================= form =============================*/
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}
/* ============== MEIA QUEIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
    .contact__container{
        grid-template-columns: 1fr !important;
        grid-gap: 2rem;
        gap: 2rem;
    }
    
}

/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
    .contact__container{
    width: var(--container-width-sm) !important;
    }

    
}
footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-bg);
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;

}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}



/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
    .permalinks{
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .footer__socials{
        margin-bottom: 2.6rem;
    }
    
}
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;

}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.6);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

}

.text-light
{
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(./assets/profile.jpeg); */

}

/* ================General styles ============================ */
.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 2rem auto;
}
h1, h2, h3, h4, h4{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
}

section > h2,
section > h5{
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);

}

section > h2{
    color: #4db5ff;
    color: var(--color-primary);
}

a{
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover{
    color: #fff;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem;
    border-radius: 0.4rem;
    cursor:pointer;
    border: 1px solid  #4db5ff;
    border: 1px solid  var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn-primary{
    background: #4db5ff;
    background: var(--color-primary);
    color:#1f1f38;
    color:var(--color-bg);

}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ============== MEIA QUEIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
    .container .testimonial__container{
        width: 60%;
    }
    
}

/* ============== MEIA QUEIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
    .container .testimonial__container{
        width: 90%;
        width: var(--container-width-sm);
    }

    .friend__review{
        width: 90%;
        width: var(--container-width-sm);
    }
    
}
